<template>
  <div class="product">
  <h1>vuex购物车demo</h1>
    <div class="title">商品信息</div>
    <table class="table">
      <thead class="thead">
        <tr>
          <th>id</th>
          <th>名称</th>
          <th>价格</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) of shoplist" :key="index">
          <td>{{ item.id }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.price }}</td>
          <td>
            <div class="btn" @click="addcart(item)">购物车</div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters('cart', ['shoplist'])
  },
  data () {
    return {

    }
  },
  methods: {
    ...mapActions('cart', ['addtocart']),
    addcart (val) {
      this.addtocart(val)
    }
  }
}
</script>
<style lang="scss" scoped>
.product {
  h1 {
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 28px;
  }
  .title {
    height: 30px;
    text-align: center;
    line-height: 30px;
  }
  .table {
    width: 100%;
    text-align: center;
    .btn {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 50px;
      height: 20px;
      background: red;
      font-size: 12px;
      text-align: center;
      line-height: 20px;
      border-radius: 2px;
    }
    tr {
      height: 60px;
      line-height: 60px;
      td {
        position: relative;
      }
    }
  }
  .table * {
    border: 1px solid #000;
  }
}
</style>
